/**
* root 
*/

:root {

    /**
     * =======================================================================
     * font guide 
     * =======================================================================
     */
    
    /* ------------- font-size ------------- */
    --fs_xxsmall : 1.2rem;
    --fs_xxsmall_sub : 1.3rem;
    --fs_xsmall : 1.4rem;
    --fs_xsmall_sub : 1.5rem;
    --fs_small : 1.6rem; 
    --fs_small_sub : 1.8rem;
    --fs_base : 2.0rem;
    --fs_base_sub : 2.2rem;
    --fs_medium : 2.4rem;
    --fs_medium_sub : 3.0rem;
    --fs_large : 3.6rem;
    --fs_large_sub : 4.0rem;
    --fs_xlarge : 4.8rem;
    --fs_xlarge_sub : 6.0rem;

    /* ------------- font-weight ------------- */
    --fw_thin : var(--fw_300); /* 해당 디자인 상 얇은 굵기 */
    --fw_base : var(--fw_400); /* 해당 디자인 상 기본 굵기 */
    --fw_medium: var(--fw_500); /* 해당 디자인 상 중간 굵기 */
    --fw_semibold : var(--fw_600); /* 해당 디자인 상 약간 굵은 굵기 */
    --fw_bold : var(--fw_700); /* 해당 디자인 상 굵은 굵기 */
    --fw_bolder : var(--fw_900); /* 해당 디자인 상 가장 굵은 굵기 - 사용 없음 */

    --fw_100 : 100;
    --fw_200 : 200;
    --fw_300 : 300;
    --fw_400 : 400;
    --fw_500 : 500;
    --fw_600 : 600;
    --fw_700 : 700;
    --fw_800 : 800;
    --fw_900 : 900;

    /* ------------- font-family ------------- */
    --ff_kor : var(--SCoreDream3Light); /* 한글 폰트*/
    --ff_eng : var(--SCoreDream3Light); /* 영문, 숫자 폰트 */
    --ff_point : var(--Pacifico), var(--SCoreDream3Light); /* 포인트 폰트 */

    --Noto-Sans-KR : 'Noto Sans KR';
    --Apple-SD-Gothic-Neo : 'Apple SD Gothic Neo';
    --Pretendard : 'Pretendard';
    --Montserrat : 'Montserrat';
    --Garamond : 'EB Garamond';
    --SCoreDream3Light : 'S-CoreDream-3Light';
    --Pacifico : 'Pacifico';

    /* ------------- line height ------------- */
    --lh_min : 1; /* 최소 line-height 값 */
    --lh_narrow : 1.3; /* 줄간격 좁음 */ 
    --lh_base : 1.5; /* 기본 line-height 값 */
    --lh_wide : 1.7; /* 줄간격 너비 넓은 경우 */
    --lh_max : 2.0; /* 줄간격 너비 변수값 기준 최대 */

    /* ------------- letter spacing ------------- */
    --ls_base : 0;
    --ls_narrow : -1px;
    --ls_wide: 0.01em;
    --ls_max : 0.1em;

}



/**
* root 
*/

:root {

    /**
     * =======================================================================
     * color guide
     * =======================================================================
     */
    
    /* ------ color pattern ------ */
    --achromatic_ori_base : 0, 0%;
    --black_ori_base : 217, 10%, 25%;
    --white_ori_base : var(--achromatic_ori_base), 97%;

    --main1_base : 37, 53%, 89%;
