html {
    overflow-x: auto;
    font-size: 62.5%;
    /* font-size 1rem = 10px ex 16px -> 1.6rem */
    *visibility: hidden;
}

@media screen and (min-width: 0\0) {
    /* IE 9, IE 10, IE 11 */
    :root,
    html {
        font-size: 10px;
    }
}

@media screen and (-ms-high-contrast: active),
(-ms-high-contrast: none) {
    /* IE 10, IE 11 */
    :root,
    html {
        font-size: 10px;
    }
}

html body {
    font: inherit;
    min-width: inherit;
    width: 100%;
    -ms-overflow-style: none;
}

html body::-webkit-scrollbar {
    *display: none;
}

html body::-webkit-scrollbar {
    width: 7px;
    /* 스크롤바의 너비 */
    height: 7px;
}

html body::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.5);
    width: 7px;
    height: 5%;
    border-radius: 10px;
    cursor: grab;
}

html body::-webkit-scrollbar-track {
    background: transparent;
    /*스크롤바 뒷 배경 색상*/
}



