.pt_section{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.pt_section:nth-child(2){
    background-color: var(--black1);
    h2{
        font-size: #{"max(6rem, 8vw)"};
        font-family: var(--ff_point);
        line-height: var(--lh_base);
    }
}
.pt_section:nth-child(3) {
    background-color: var(--main2);
}
.pt_section:nth-child(4) {
    background-color: var(--main1);
}
.pt_section:nth-child(5) {
    background-color: var(--white1);
}
.pt_section:nth-child(2) .text_box *{
    color: var(--white1);
}
.pt_section:nth-child(3) .text_box * {
    color: var(--main1);
}
.pt_section:nth-child(4) .text_box * {
    color: var(--main2);
}
.pt_section:nth-child(5) .text_box * {
    color: var(--black1);
}
.pt_section .text_box{
    text-align: center;
}
.pt_section h2{
    font-size: #{"max(4rem, 6vw)"};
    font-family: var(--ff_point);
    line-height: var(--lh_base);
}
.pt_section p{
    font-size: #{"max(1.6rem, 1vw)"};
    line-height: var(--lh_base);
}

.pt_navigation{
    position: fixed;
    top: 50%; 
    right: #{"min(5%, 3rem)"};
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    z-index: 99;

    button{
        text-indent: -9999px;
        overflow: hidden;
        display: block;
        width: 1rem;
        height: 1rem;
        margin: 0.5rem 0;
        background: var(--main2);
        border-radius: 50%;
        transition: .4s ease-in-out;
        
        &:last-child{
            border-radius: 0;
            background: none;
            pointer-events: none;
        }
        &:first-child{
            border-radius: 0;
            background: none;
            pointer-events: none;
        }

        
    }

    &[class*="active_"]{
        button{
            &[disabled]:not(:last-child):not(:first-child){
                background: var(--black1);
            }
        }

        &.active_0{
            button:not(:last-child):not(:first-child){
                &[disabled]{
                    background: var(--white1);
                }
            }
        }

        &.active_1{
            button:not(:last-child):not(:first-child):not([disabled]){
                background: var(--main1);
            }
        }
    }

    
}

#pt_fullpage{
    .pt_intro{

        .text_box{
            display: inline-grid;
            column-gap: #{"max(8rem, 10vw)"};
            grid-auto-flow: column;
            align-items: center;
            justify-content: center;
        }

        //Setup
        @mixin stagger-anim-delay($i, $initial-delay, $stagger-delay) {
        @while $i > 0 {
            &:nth-of-type(#{$i}) {
            animation-delay: $initial-delay + $stagger-delay * $i;
            }
            $i: $i - 1;
        }
        }
        $baseFontSize: 16px;
        $baseLineHeight: 1;
        $chartGap: 20px;
        $chartCount: 1;
        $chartOneCount: 6;
        $chartTwoCount: 0;
        $chartThreeCount: 0;
        $totalChartCount: $chartOneCount + $chartTwoCount + $chartThreeCount;
        $titleGap: 10px;
        $titleHeight: $baseFontSize * $baseLineHeight + $titleGap;
        $chartBarHeight: 30px;
        $chartBarGap: 10px;

        //Demo
        .skills {
        width: #{"max(36rem, 30vw)"};
        height: $totalChartCount * ($chartBarHeight + $chartBarGap) + ($chartCount * ($chartGap + $titleHeight));
        margin: auto;
        position: relative;
        }
        .lines {
        height: 100%;
        position: relative;
        }
        .line {
        --bg : hsla(var(--white_ori_base),0.3);
        height: inherit;
        width: 2px;
        position: absolute;
        background: var(--bg);
        &.l--0 {
            left: 0;
        }
        &.l--25 {
            left: 25%;
        }
        &.l--50 {
            left: 50%;
        }
        &.l--75 {
            left: 75%;
        }
        &.l--100 {
            left: calc(100% - 1px);
        }
        }
        .line__label {
        display: block;
        width: 100px;
        text-align: center;
        position: absolute;
        bottom: -20px;
        right: -50px;
        font-size: 1.4rem;
        &.title {
            
        }
        }
        .charts {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        }
        .chart {
        &:not(:first-child) {
            margin-top: $chartGap;
        }
        }
        .chart__title {
        display: block;
        margin: 0 0 20px;
        font-weight: bold;
        opacity: 0;
        animation: 1s anim-lightspeed-in ease forwards;
        .chart--dev & {}
        .chart--prod & {
            animation-delay: 3.3s;
        }
        .chart--design & {
            animation-delay: 4.5s;
        }
        }
        .chart--horiz {
        overflow: hidden;
        }
        .chart__bar {
        $border-rad: 3rem;
        height: $chartBarHeight;
        margin-bottom: $chartBarGap;
        background: linear-gradient(to left, var(--black1), var(--black1));
        border-top-right-radius: $border-rad;
        border-bottom-right-radius: $border-rad;
        opacity: 0;
        
        
        }
        .chart__label {
        padding-left: 10px;
        line-height: 30px;
        color: var(--main1);
        font-size: 1.2rem;
        }
        //Keyframes
        @keyframes anim-lightspeed-in {
        0% {
            transform: translateX(-200%);
            opacity: 1;
        }
        100% {
            transform: translateX(0);
            opacity: 1;
        }
        }



        &.active{
            .chart__bar {
                animation: 1.5s anim-lightspeed-in ease forwards;
                .chart--dev & {
                    @include stagger-anim-delay($chartOneCount, 0.5s, 0.2s);
                }
                .chart--prod & {
                    @include stagger-anim-delay($chartTwoCount, 3.8s, 0.2s);
                }
                .chart--design & {
                    @include stagger-anim-delay($chartThreeCount, 5s, 0.2s);
                }
            }
        }
    }


    .pt_detail{
        
        position: relative;
        overflow: hidden;

        .pt_detail_img{
            display: block;
            background-size: cover;
            background-position: center;
            filter: blur(10px);
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: 100%;
            transform: translate(-50%,-50%);
            transition: .4s ease-in-out;
        }

        .text_box{
            position: relative;
            z-index: 1;
            display: inline-grid;
            column-gap: #{"max(8rem, 10vw)"};
            grid-auto-flow: column;
            align-items: center;
            justify-content: center;
        }
        .pt-detail-swiper{
            width: 35vw;
            margin-top: #{"max(3rem, 5vh)"};
            overflow: hidden;
            box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.54);
            -webkit-box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.54);
            -moz-box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.54);

            .swiper-slide{
                padding: 7rem;
                
                &::after{
                    display: block;
                    content: "";
                    width: 100%;
                    height: 100%;
                    filter: blur(15px);
                    position: absolute;
                    top: 0;
                    left: 0;
                    border-radius: 2rem;
                    overflow: hidden;
                }

                mark{
                    background: var(--black1);
                }

                div{
                    padding: 2rem;
                    position: relative;
                    z-index: 1;
                }
                img{
                    position: relative;
                    z-index: 1;
                }
                &:not(.swiper-slide-active){
                    div{
                        opacity: 0;
                    }
                }
            }

            .swiper-button-prev{
                left: 2rem;
                right: auto;
            }
            .swiper-button-next{
                right: 2rem;
                left: auto;
            }
            .swiper-pagination{
                bottom: 3rem;
                --swiper-pagination-bullet-inactive-color: var(--black1);
                --swiper-pagination-color : var(--main2);

                .swiper-pagination-bullet{
                    mix-blend-mode: difference;
                }
            }
        }   

    }

}




@media all and (max-width:1299px){

    #pt_fullpage{
        .pt_intro{
            .text_box{
                column-gap: #{"max(8rem, 10vw)"};
                grid-auto-flow: row;

                .skills{
                    width: #{"max(28rem, 50vw)"};
                    margin-top: #{"max(3rem, 5vh)"};
                }
            }
        }

        .pt_detail{
            .text_box{
                column-gap: #{"max(8rem, 10vw)"};
                grid-auto-flow: row;
            }
            .pt-detail-swiper{
                width: #{"max(30rem, 55vw)"};
                margin-top: #{"max(3rem, 5vh)"};
                overflow: hidden;

                .swiper-slide{
                    padding: 3rem;

                    div{
                        padding: 2rem 0;
                    }
                }

                .swiper-button-prev,
                .swiper-button-next{
                    display: none;
                }
                .swiper-pagination{
                    bottom: 2rem;
                }
            }   

        }
    }
}


